import service from './axios';

//气瓶档案管理
//获取表格所有数据
export const getAllGasList = (val) => {
    console.log(val)
    return service({
        url:`/bottle/findPage?page=${val.currpage}&rows=${val.pageSize}`,
        method: 'get',
    })
};
//查询搜索框内容
export const getSearchGasList = (val) => {
    console.log(val)
    return service({
        url:`/bottle/findPage?page=${val.currpage}&rows=${val.pageSize}&gasId=${val.gasId}&medium=${val.medium}`,
        method: 'get',
    })
};
//添加信息
export const getAllGasBottle = (val) => {
    console.log(val)
    return service({
        url:`/bottle`,
        method: 'post',
        headers:{'contentType': 'application/json;charset=UTF-8'},
        data: val
    })
};
